import React, {useState,useCallback ,useEffect} from 'react';
import ConfirmInfo from '../component/ConfirmInfo';
import SacomOtpRequest from './SacomOtpRequest';
import SacomOtpValidate from './SacomOtpValidate';

const SacomValidation = () => {

  useEffect(() =>{
    console.log("SacomValidation loaded");
    // if (!document.getElementById('VIET')) {
    //     const scriptVietnamEwallet = document.createElement('script');
    //     scriptVietnamEwallet.id = 'VietnamEwallet';
    //     scriptVietnamEwallet.src = 'https://isrnd.bccard.com:56443/src/static/js/bcsrcsdk.vietnam.ewallet.js';
    //     document.body.appendChild(scriptVietnamEwallet);
    //   }

}, []);


const [otpRequestState, setOtpRequestState] = useState('request_idle'); // 'request_idle', 'request_inprogress', 'request_ok', 'request_fail'
const [otpValidationState, setOtpValidationState] = useState('validate_idle');  // 'validate_idle', 'validate_inprogress','validate_ok', 'validate_fail'


const onOtpRequestStateChanged = useCallback( function(state) {
  setOtpRequestState(state);
} ,[]);

const onOtpValidateStateChanged = useCallback( function(state) {
  setOtpValidationState(state);
} ,[]);

  //SacomValidation화면에서는 각 단계별 화면 컨트롤을 진행한다
  return (
    <>
    {console.log(otpRequestState)}
    {console.log(otpValidationState)}

      <SacomOtpRequest onOtpRequestStateChanged={onOtpRequestStateChanged} />
      <div className="mt15" />
      {otpRequestState==="request_ok" ?  <SacomOtpValidate onOtpValidateStateChanged={onOtpValidateStateChanged} /> : '' }
      <div className="mt15" />
      {otpRequestState==="request_ok" && otpValidationState==="validate_ok" ?  <ConfirmInfo /> : '' }
      
    </>
  );
};

export default SacomValidation;
