import React, { useState } from 'react';
import './InputDetail.scss';

const InputDetail = (props) => {
  const { state, placeholder, onStateChanged } = props;

  const [inputValue, setInputValue] = useState('');
  const [errMessage, setErrMessage] = useState('');

  const onClick = () => {
    console.log('InputDetail onClicked!');
    console.log(window.SRCSDK_VIETNAM);
    console.log(inputValue);
    setErrMessage(inputValue);

    onStateChanged('ok');
  };

  const onChange = (e) => {
    setInputValue(e.target.value);
  };

  return (
    <div>
      <div className="gray_box01 mt20">
        <div className="input_wrap01">
          <input
            onChange={onChange}
            type="text"
            id="inputDetail"
            className="input_type"
            placeholder={props.placeholder}
          />
          <span className="btn btn_gray_b1 line2">
            <a onClick={onClick} href="#link">
              {props.btnValueEng} <br /> {props.btnValueViet}{' '}
            </a>
          </span>
        </div>
        <p className="mt10 fc_red">{errMessage}</p>
      </div>
    </div>
  );
};

InputDetail.defaultProps = {
  placeholder: '+84 1234 5678',
  btnValueEng: 'Request',
  btnValueViet: '(Xác nhận)',
};

export default InputDetail;
