import React from 'react';
import CancleButton from './CancleButton';
import ConfirmButton from './ConfirmButton';
import './Prompt.scss'

const Prompt = props => {
    const { type } = props; // cancle, confirm, both

    const resolveLayout = () => {
        console.log(type);
        if(type === 'cancle') {
            return <CancleButton />;
        } else if (type ==='both') {
            return (
                <>
                  <ConfirmButton />
                  <div className="pr10"></div>
                  <CancleButton />
                </>
            );
        } else if (type === 'confirm') {
            return <ConfirmButton />;
        }
        return <ConfirmButton />;
    }

    


    return (
        <div className='btn_wrap'>
            {resolveLayout()}
        </div>
    );
};

Prompt.defaultProps = {
    type: 'cancle',
}

export default Prompt;
