import React, {useState, useCallback} from 'react';
import InputDetail from '../component/InputDetail';
import StepLabel from '../component/StepLabel';
import Timer from '../component/Timer';

const SacomOtpValidate = ({onOtpValidateStateChanged}) => {
  const stepProps3 = ' Sacom Step 3-4.';
  const valueProps3 =
    'Enter OTP Number and Click Request(Nhập mã OTP và  Nhấn nút xác nhận)';

  const [state, setState] = useState('idle'); //'idle', 'inprogress', 'ok', 'fail'

  const onStateChanged = useCallback(function (state) {
    setState(state);
    onOtpValidateStateChanged('validate_' + state);
  }, []);

  return (
    <div>
      <StepLabel step={stepProps3} value={valueProps3} />
      <InputDetail placeholder={'1234 5679'} onStateChanged={onStateChanged}/>
      <Timer />
    </div>
  );
};

export default SacomOtpValidate;
