import React, {useState, useCallback} from 'react';
import InputDetail from '../component/InputDetail';
import StepLabel from '../component/StepLabel';

const SacomOtpRequest = ({ onOtpRequestStateChanged }) => {
  const stepProps2 = 'Step 2.';
  const valueProps2 ='Enter Mobile Phone Number and Click request(Nhập số điện thoại và Nhấn nút xác nhận)';

  const [state, setState] = useState('idle'); //'idle', 'inprogress', 'ok', 'fail'

  const onStateChanged = useCallback( function(state) {
    setState(state);
    onOtpRequestStateChanged('request_' + state);
  } ,[]);

  return (
    <div>
      {console.log(state)}
      <StepLabel step={stepProps2} value={valueProps2} />
      <InputDetail placeholder={'+84 1234 5679'} onStateChanged={onStateChanged} />
    </div>
  );
};

export default SacomOtpRequest;
